import React from 'react';
import m from './Posts.module.css'

type MessagePropsType = {
    message: string
    counts: number
}

export const Post:React.FC<MessagePropsType> = (props) => {
    return (
        <div>
                <div className={m.item}>
                    <img alt=''
                        src='data:image/png;base64,'/>
                    {props.message}
                    <div>
                        <span>likes:{props.counts}</span>
                    </div>
                </div>
        </div>
    )
}

